<template>
  <div class="van-select-dict">
    <van-select-dict @dictChange="handleChange" label="()" label-width="100px" input-align="right" dictType="AAC004" placeholder="" v-model="value1" ></van-select-dict>
    <van-select-dict @dictChange="handleChange" label="()" required label-width="100px" input-align="right" dictType="AAC004" placeholder="" v-model="value2" ></van-select-dict>
    <van-select-dict @dictChange="handleChange" label="(1)" label-width="110px" input-align="right" :dictType="{type: 'AAC004', filters: '9', reverse: true}" placeholder="" v-model="value3" ></van-select-dict>
    <van-select-dict @dictChange="handleChange" label="(2)" :filterDataFun="filterDataFun" label-width="110px" input-align="right" :dictType="'AAC004'" placeholder="" v-model="value13" ></van-select-dict>
    <van-select-dict @dictChange="handleChange" label="(1)" label-width="110px" input-align="right" :dictType="'AAC004'" :disableObj="{disableValue: '9'}" placeholder="" v-model="value4" ></van-select-dict>
    <van-select-dict @dictChange="handleChange" label="(2)" :disabledDataFun="disabledDataFun" label-width="110px" input-align="right" :dictType="'AAC004'"  placeholder="" v-model="value14" ></van-select-dict>
    <van-select-dict @dictChange="handleChange" label="(+)" label-width="120px" input-align="right" :dictType="{type: 'AAC004', filters: '1', reverse: true}" :disableObj="{disableValue: '9'}" placeholder="" v-model="value5" ></van-select-dict>
    <van-select-dict @dictChange="handleChange" label="(keyValue)" keyValue label-width="120px" input-align="right" dictType="AAC004" placeholder="" v-model="value6" ></van-select-dict>
    <van-select-dict @dictChange="handleChange" label="(1)" label-width="120px" input-align="right" :data="list1" placeholder="" v-model="value7" ></van-select-dict>
    <van-select-dict @dictChange="handleChange" label="(2)" :format="{label: 'label2', value: 'value2'}" label-width="120px" input-align="right" :data="list2" placeholder="" v-model="value8" ></van-select-dict>
    <van-select-dict @dictChange="handleChange" label="(3)" label-width="120px" input-align="right" :data="list3" placeholder="" v-model="value9" ></van-select-dict>
    <van-select-dict @dictChange="handleChange" label="()" label-width="100px" input-align="right" dictType="AAC004" placeholder="" v-model="value10" ></van-select-dict>
  </div>
</template>

<script>
export default {
  data() {
    return {
      value1: "",
      value2: "",
      value3: "",
      value4: "",
      value5: "",
      value6: "",
      value7: "",
      value8: "",
      value9: "",
      value10: "1",
      value13: "",
      value14: "",
      list1: [],
      list2: [],
      list3: ["", "", ""]
    }
  },
  created() {
    setTimeout(() => {
      this.list1 = [ 
        {
          label: "",
          value: "1"
        },
        {
          label: "",
          value: "2"
        },
        {
          label: "",
          value: "3"
        }
      ]
      this.list2 = [ 
        {
          label2: "",
          value2: "4"
        },
        {
          label2: "",
          value2: "5"
        },
        {
          label2: "",
          value2: "6"
        }
      ]
    }, 1000)
  },
  methods: {
    handleChange(val) {
      console.log(val)
    },
    filterDataFun(list) {
      return list.filter(item => ["1", "2"].includes(item.value))
    },
    disabledDataFun(list) {
      return list.map(item => {
        return {
          ...item,
          disabled: ["9"].includes(item.value)
        }
      })
    }
  }
}
</script>

