<template>
  <div class="van-cascader-dict">
    <van-cascader-dict @dictChange="handleChange" label="()" label-width="110px" title="" input-align="right"  dictType="city" placeholder="" v-model="value1" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="(1)" label-width="110px" title="" input-align="right"  :dictType="{type: 'city', filters: '35062403', reverse: true}" placeholder="" v-model="value2" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="(2)" :filterDataFun="filterDataFun" label-width="110px" title="" input-align="right"  :dictType="{type: 'city', filters: '35062403', reverse: true}" placeholder="" v-model="value3" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="(1)" label-width="110px" title="" input-align="right" :disableObj="{disableValue: '35062403'}" :dictType="'city'" placeholder="" v-model="value4" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="(2)" :disabledDataFun="disabledDataFun" label-width="110px" title="" input-align="right"  :dictType="'city'" placeholder="" v-model="value5" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="(+)" label-width="110px" title="" input-align="right" :dictType="{type: 'city', filters: '35062303', reverse: true}" :disableObj="{disableValue: '35062403'}"  placeholder="" v-model="value6" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="(+)" label-width="110px" title="" input-align="right" :dictType="{type: 'city', filters: '35062303', reverse: true}" :disableObj="{disableValue: '35062403'}"  placeholder="" v-model="value6" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="()" maxLevel="2" label-width="110px" title="" input-align="right" :dictType="'city'"  placeholder="" v-model="value8" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="()" minSelectLevel="3" label-width="110px" title="" input-align="right" :dictType="'city'"  placeholder="" v-model="value9" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="()" tips="" :minSelectLevel="3" label-width="110px" title="" input-align="right" :dictType="'city'"  placeholder="" v-model="value10" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="()" label-width="110px" :finish="true" title="" input-align="right" :dictType="'city'"  placeholder="" v-model="value11" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="()" label-width="110px" title="" input-align="right" cascaderPlaceholder="" :dictType="'city'"  placeholder="" v-model="value12" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="()" label-width="110px" title="" input-align="right" cancelButtonText="" confirmButtonText="" :dictType="'city'"  placeholder="" v-model="value13" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="()" label-width="110px" title="" input-align="right" spacer="," :dictType="'city'"  placeholder="" v-model="value14" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="()" defaultValue="" label-width="110px" title="" input-align="right" :dictType="'city'"  placeholder="" v-model="value15" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="(1)" :data="list1" label-width="110px" title="" input-align="right" placeholder="" v-model="value16" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="(2)"  :props="props" :data="areaList" label-width="110px" title="" input-align="right"  placeholder="" v-model="value17" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="(3)" :props="props" :treeData="areaTreeData" label-width="110px" title="" input-align="right"  placeholder="" v-model="value20" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="(4)" :props="props" :treeData="areaTreeData" label-width="110px" title="" input-align="right"  placeholder="" v-model="value21" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="()" :props="props" :data="areaList" label-width="110px" title="" input-align="right"  placeholder="" v-model="value18" ></van-cascader-dict>
    <van-cascader-dict @dictChange="handleChange" label="()"  dictType="city"  label-width="110px" title="" input-align="right"  placeholder="" v-model="value19" ></van-cascader-dict>
  </div>
</template>

<script>
import areaList from "./areaData"
import areaTreeData from "./areaTreeData"
export default {
  data() {
    return {
      areaList,
      areaTreeData,
      value1: "",
      value2: "",
      value3: "",
      value4: "",
      value5: "",
      value6: "",
      value7: "",
      value8: "",
      value9: "",
      value10: "",
      value11: "",
      value12: "",
      value13: "",
      value14: "",
      value15: "21312421",
      value16: "",
      value17: "",
      value18: "350203",
      value19: "35062401",
      value20: "",
      value21: "350203",
      list1: [],
      props: {
        text: "areaName",
        value: "id"
      }
    }
  },
  created() {
    this.$getDictObjByDictTypes("country").then(data => {
      this.list1 = data.country
    })
  },
  methods: {
    handleChange(val) {
      console.log(val)
    },
    filterDataFun(list) {
      return list.filter(item => !["35062403"].includes(item.id))
    },
    disabledDataFun(list) {
      return list.map(item => {
        return {
          ...item,
          disabled: ["35062403", "35062301"].includes(item.id)
        }
      })
    }
  }
}
</script>

